<template>
  <div class="showView">
    <BlogItem v-for="item in listArr"
    :key="item.id"
    :itemObj="item"
    />
    <div class="pageGroup">
      <div class="btn Left" @click="btnLeft">&lt; 上一页</div>
      <div class="btn Right" @click="btnRight">下一页 &gt;</div>
    </div>
    <div class="toast" v-show="showToast">数据加载中...</div>
  </div>
</template>

<script>
import BlogItem from '@/components/BlogItem'
export default {
  data () {
    return {
      showToast: false,
      page: 0,
      listArr: [
        // {title:"标题一",content:"内容1111"},
        // {title:"标题二",content:"内容2222"},
        // {title:"标题三",content:"内容3333"}
      ],
      length: 0
    }
  },
  components: {
    BlogItem
  },
  created () {
    this.getData()
  },
  methods: {
    btnLeft () {
      this.page = this.page - 5
      if (this.page < 0) {
        alert('已经是第一页了')
        this.page = 0
        return
      }
      this.showToast = true
      this.getData()
      this.scrTop()
    },
    btnRight () {
      this.page = this.page + 5
      if (this.page > length) {
        alert('已经到最后一页了')
        this.page = this.page - 5
        return
      }
      this.showToast = true
      this.getData()
      this.scrTop()
    },
    getData () {
      // this.axios({
      //   url: '/posts'
      // }).then(res => {
      //   this.listArr = res.data.splice(this.page, 5)
      //   this.showToast = false
      // })
      console.log('ok')
      this.axios.get('/test').then(res => {
        console.log(res.data.length)
        this.listArr = res.data
        this.length = res.data.length
        this.showToast = false
      })
    },
    scrTop () {
      if (document.body.scrollTop) {
        document.body.scrollTop = 0
      } else {
        document.documentElement.scrollTop = 0
      }
    }
  }
}
</script>

<style lang="less" scoped>
.showView{
  padding: 30px;
}
.pageGroup{
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  .btn{
    border: 1px solid rgb(6, 104, 161);
    padding: 10px 20px;
    color: rgb(6, 104, 161);
    font-size: 18px;
    margin: 0 10px;
    cursor: pointer;
    transition: 0.01s;
    &:hover{
      background-color: rgb(6, 104, 161);
      color: #fff;
    }
  }
}
.toast{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0,0,0,0.3);
  color: #fff;
  display: flex;
  font-size: 30px;
  justify-content: center;
  align-items: center;
}

</style>
